.wrapper {
  position: relative;

  .colors {
    position: absolute;
    width: 0;
    overflow: hidden;
    transition: all ease 0.1s;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-gap: 10px;
    gap: 10px;
    z-index: 10;
    background: white;
    box-shadow: 0 0 10px 4px #ececec;
    border-radius: 2px;
    left: 99%;
    top: 0;
    padding: 10px;
    opacity: 0;
  }

  &:hover {
    .colors {
      opacity: 1;
      width: 115px;
    }
  }

  button.colorButton {
    min-width: 15px;
    width: 15px;
    height: 15px;
    border: 0;
    border-radius: 10px;
    cursor: pointer;
    transition: all ease 0.3s;
    padding: 0;

    &:hover {
      transform: scale(1.8);
    }

    &.transparent {
      background: transparent;
      border: 1px solid #eaeaea;
    }
  }
}
